body {
  font-size: 18px;
  background: #1e1e1e;
  font: 16px/1.5 Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  color: #efeff0;
}

#root {
  display: flex;
  justify-content: center;
}

button {
  padding: 16px;
  font: 16px/1.5 Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;;
  font-size: 1.5em;
  background-color: #1e1e1e;
  color: gold;
  border: 1px gold solid;
  border-radius: 16px;
  cursor: pointer;
  max-width: 200px;
}

.call-to-action-link {
  padding: 16px;
  font-size: 1.5em;
  color:skyblue;
  border: 2px skyblue solid;
  border-radius: 16px;
  margin-top: 16px;
  max-width: 200px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.call-to-action-link:hover {
  color: #1e1e1e;
  border: 2px solid #1e1e1e;
  background: skyblue;
  transition: 0.8s;
}

.title {
  height: 100vh;
  display: inline-grid;
  align-content: center;
  justify-content: center;
  animation: 0.8s ease-out 0s 1 slideInFromLeft;
}

.hero-name {
  color:gold;
  font-size: clamp(0.8em, 4vw, 2.5em);
  font-family: Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  font-weight: normal;
  margin: 0 0 16px 0px;
}

.tagline {
  font-size: clamp(2em, 10vw, 6em);
  line-height: clamp(1em, 10vw, 1.1em);
  font-family: Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  font-weight:400;
  margin: 0 0 0px 0px;

}

.intro {
  font-size: clamp(1em, 3vw, 1.8em);
  font-family: Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  max-width: 80%;
  margin-top: 40px;
}

.scroll-prompt {
  color: gold;
  /* transform: rotate(90deg); */
  max-width: fit-content;
  margin-top: 80px;
}

.headshot {
  justify-content: center;
  align-items: center;
  align-content: center;
  display: flex;
  padding: 0;
}

.headshot:hover{
  color: gold;
  transition: 0s;
}

.headshot img {
  width: 56px;
  height: 56px;
  object-fit: cover;
  border-radius: 50%;
  margin: 0 8px 0 0;
  padding: 0;
}

.headshot p {
  padding: 0;
}

.highlight {
  color: gold;
  padding-right: 8px;
}

.featured h2 {
  font-family: Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
  font-weight: 500;
  color: #efeff0;
  font-size: clamp(1.5em, 4vw, 2em);
}

.featured{
  max-width: 900px;
}

.project-list {
  display: flex;
  list-style-type: none;
  padding-top: 0px;
  margin-top: 0px;
  flex-wrap: wrap;
  padding: 0;
}

.project-list-item {
  position: relative;
  display: grid;
  -webkit-box-align: center;
  align-items: center;
  grid-template-columns: repeat(10, 20px 1fr);
  visibility: visible;
  opacity: 1;
  width: 100%;
  margin-bottom: 40px;
}

.project-list-item:nth-child(even) .list_item_content {
  grid-column-start: 1;
  grid-column-end: 13;
  grid-row-start: 1;
  grid-row-end: -1;
}

.project-list-item:nth-child(even) a {
  justify-content: end;
}

.list_item_content {
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  background-color: #2e2e2e;
  z-index: 5;
  padding: 32px;
  grid-column-start: 10;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: -1;
  border-radius: 16px;
  box-shadow:  8px 8px 16px #111;
}

.list_item_content p {
  margin: 8px 0 0 0;
}

.tech {
  display: inline-flex;
  padding-top: 16px;

}

.tech p {
  padding-right: 16px;
  color: gold;
  margin: 0;
}

.project-list-item img {
  display: block;
  position: relative;
  z-index: 1;
  max-width: 50%;
  border-radius: 16px;
  filter: grayscale(100%);
  /* opacity: 0.25; */
}

.project-list-item:hover img {
  transition: 0.8s;
  filter: grayscale(0%);
}

.project-list-item a {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: -1;
  display: flex;
  justify-content: start;
  text-decoration: none;
  color: #efeff0;
}

h3, h4 {
  margin: 0;
}

nav {
  /* border-bottom: 2px solid #2f2f2f; */
  background: #1e1e1e;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 50vw;
  padding: 3.125rem 0;
  z-index: 10;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

nav li {
  display: inline-block;
}

nav a {
  text-decoration: none;
  color: #efeff0;
  font-weight: bold;
  padding: 24px;
}

nav a:hover {
  color: gold;
  transition: 0.8s;
}

#page-body {
  max-width: 50vw;
  margin: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font: 16px/1.5 Calibre, Inter, "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.down-arrow {
  position: absolute;
  top: calc(100vh - 80px);
  left: calc(50% - 14px);
  width: 0;
  height: 30px;
  border: 2px solid;
  border-radius: 2px;
}

.down-arrow:after {
  content: " ";
  position: absolute;
  top: 12px;
  left: -10px;
  width: 16px;
  height: 16px;
  border-bottom: 4px solid;
  border-right: 4px solid;
  border-radius: 4px;
  transform: rotateZ(45deg);
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.8;
  }
}

@keyframes jumpInfinite {
  0% {
    margin-top: 0;
  }
  50% {
    margin-top: 20px;
  }
  100% {
    margin-top: 0;
  }
}

@keyframes slideInFromLeft {
  0% {
    transform: translate(-2%, -2%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.down-arrow {
  animation: 0.8s ease-out 1s 1 appear, jumpInfinite 1.5s infinite;
  animation-delay: 4s;
  opacity: 0;
  z-index: 0;
  animation-fill-mode: forwards; 
}

@media only screen and (max-width: 900px)  {
  
.project-list {
  display: flex;
  list-style-type: none;
  padding-top: 0px;
  margin-top: 0px;
  flex-wrap: wrap;
  padding: 0;
}

.project-list-item {
  position: relative;
  display: grid;
  -webkit-box-align: end;
  align-items: end;
  visibility: visible;
  opacity: 1;
  width: 100%;
  margin-bottom: 16px;
}

.project-list-item img {
  display: block;
  position: relative;
  z-index: 1;
  max-width: 80%;
  margin-bottom: 20%;
  display: none;
}

.project-list-item a {
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: #efeff0;
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: -1;
}

.list_item_content {
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  background-color: #2e2e2e;
  z-index: 5;
  padding: 32px;
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: -1;
  opacity: 0.9;
}

.list_item_content p {
  margin: 8px 0 0 0;
}

.tech {
  display: inline-flex;
  padding-top: 16px;
  flex-wrap: wrap;
}

.tech p {
  padding-right: 16px;
  color: gold;
  margin: 0;
}

.project-list-item:nth-child(even) .list_item_content {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 1;
  grid-row-end: -1;
}

#page-body {
  max-width: 80vw;
}
nav {
  width: 80vw;
}
.headshot p {
  display: none;
}

}